/********************************************* Nav Bar */
nav {
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  color: white;
  width: 80px;
  height: 100vh;
  z-index: 100;

  position: fixed;
  top: 0;
  left: 0px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  transition: transform 0.5s ease-in-out;
}

nav.show {
  transform: translateX(0%);
}

.nav-container {
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.nav-container a {
  text-decoration: none;
  font-size: 12px;

  width: 100%;
  height: 80px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  transition: transform 0.2s ease-in-out;
}
.nav-container a:hover {
  transform: scale(1.3);
}

.nav-container img {
  width: 35px;
  height: 35px;
}

.menu-btn {
  background-color: transparent;
  color: var(--main-blue);

  border: 0;
  font-size: 3rem;
  padding: 0 10px;

  display: none;
  position: absolute;
  bottom: 10px;
  left: 10px;
  transform: translateX(100%);
}
.menu-btn:focus,
.menu-btn:active {
  outline: none;
}

@media (max-width: 1140px) {
  nav {
    justify-content: flex-end;
    transform: translateX(-100%);
  }
  .nav-container {
    flex-direction: column-reverse;
    margin-bottom: 50px;
  }
  .menu-btn {
    display: block;
  }
}
