.contact-body {
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  padding: 20px;
  margin-bottom: 20px;
  text-align: center;
  transition: transform 0.5s ease-in-out;
}

.contact-body:hover {
  transform: scale(1.1);
}

.contact-body a {
  text-decoration: none;
}

.contact-body p {
  font-size: 1.5rem;
  margin-top: 20px;
  word-wrap: break-word;
}

.contact-body .logo {
  font-size: 10rem;
  margin: 50px 0;
}

@media (max-width: 540px) {
  .contact-body .logo {
    font-size: 5rem;
  }
  .contact-body p {
    font-size: 1rem;
  }
}
