.skill-card {
  box-shadow: var(--main-box-shadow);
  background-color: var(--main-card-backcolor);

  margin: 20px 0px;
  height: 500px;
  padding: 10px 15px;

  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.skill-card:hover {
  transform: scale(1.05);
}

.skill-card a {
  text-decoration: none;
  color: white;
  width: 100%;
  height: 100%;
}

/* ****************** content ****************** */
.skill-card-content {
  font-family: 'Jura', sans-serif;
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* ****************** single logo ****************** */
.skill-card-content .logo {
  flex: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 200px;
}

.skill-card-content .logo img {
  width: 150px;
  height: 150px;
}

/* ****************** multi logo ****************** */
.skill-card-content .multi-logo {
  flex: 2;
  width: 60%;
  height: 80px;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.skill-card-content .multi-logo img {
  width: 60px;
  height: 60px;
}

/* ****************** card description ****************** */
.skill-card-content .description {
  flex: 1;
  text-align: center;
  font-size: 1.5rem;
  padding: 0 20px;
}

/* ****************** View Projects Text ****************** */
.skill-card small {
  visibility: hidden;
  width: 100%;
  text-align: right;
}

.skill-card:hover small {
  visibility: visible;
}
