.project {
  font-family: 'Jura', sans-serif;
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  padding: 40px 40px;
  margin-top: 40px;
  position: relative;
}

.project h1 {
  font-size: 4rem;
  color: var(--main-blue);
}

.project h3 {
  color: var(--main-blue);
}

.project a {
  color: var(--main-font-color);
}

.project .techs {
  position: absolute;
  right: 20px;
  top: 20px;
}

.project .techs img {
  height: 25px;
  margin: 0 5px;
}

.project-body {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.screen-shot {
  flex: 2;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 50px 0;
}

.screen-shot img {
  max-width: 300px;
  margin-bottom: 5px;
}

.project-body .description {
  flex: 5;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.project-body .description p,
.project-body .description ul {
  font-size: 1.2rem;
}

@media (max-width: 1140px) {
  .screen-shot img {
    max-width: 300px;
  }
}
@media (max-width: 970px) {
  .screen-shot img {
    max-width: 200px;
  }
}

@media (max-width: 720px) {
  .screen-shot {
    flex-direction: column;
  }

  .project .techs {
    right: 10px;
    top: 10px;
  }
  .project .techs img {
    height: 15px;
  }

  .project h1 {
    font-size: 2rem;
    color: var(--main-blue);
  }
}
