@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap');

/* ******************************************* Intro ******************************************* */
.intro-container {
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);

  position: relative;
  display: flex;
  justify-content: space-between;

  transition: transform 0.5s ease-in-out;
}

.intro-container:hover {
  transform: scale(1.05);
  animation: glowing 1s ease-in-out;
  animation-fill-mode: forwards;
}
.intro-container:hover img {
  animation: glowing 2s ease-in-out;
  animation-fill-mode: forwards;
}

.intro-container .logo {
  z-index: 1;
  position: absolute;
  top: -20px;
  right: 30px;
}

.intro-container img {
  max-width: 250px;
}

/* ******************************************* Intro Text ******************************************* */
.intro-text {
  /* background-color: red; */
  font-family: 'Roboto', sans-serif;
  padding: 150px 100px;
  z-index: 2;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
}

.intro-text h1 {
  letter-spacing: 15px;
  font-size: 6rem;
}

.intro-text h2 {
  font-size: 5rem;
  letter-spacing: 5px;
}

.intro-text small {
  font-family: 'Jura', sans-serif;
  font-size: 1.5rem;
  letter-spacing: 2px;
}

/* ******************************************* Pitch Text ******************************************* */
.pitch-container {
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  font-family: 'Jura', sans-serif;
  font-size: 2rem;
  padding: 40px;
  text-align: left;
  transition: transform 0.5s ease-in-out;
}

.pitch-container:hover {
  transform: scale(1.05);
  animation: glowing 1s ease-in-out;
  animation-fill-mode: forwards;
}

.pitch-container a{
  color: var(--main-blue);
}

.pitch-container p{
  margin-bottom: 25px;
}

/* ******************************************* Media Adjustment ******************************************* */
@media (max-width: 1140px) {
  .intro-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .intro-container .logo {
    position: relative;
    top: -40px;
    right: 0;
    margin: 20px auto;
  }

  .intro-text {
    padding: 100px;
    align-items: center;
  }
}

@media (max-width: 960px) {
  .intro-text {
    padding: 90px;
  }
  .intro-text h1 {
    font-size: 5rem;
    letter-spacing: 5px;
  }
  .intro-text h2 {
    font-size: 4rem;
    letter-spacing: 5px;
  }
}

@media (max-width: 720px) {
  .pitch-container {
    font-size: 1.2rem;
  }
  .intro-text {
    padding: 70px;
  }
  .intro-text h1 {
    font-size: 4rem;
    letter-spacing: 5px;
  }
  .intro-text h2 {
    font-size: 3rem;
    letter-spacing: 5px;
  }
  .intro-text small {
    font-size: 1rem;
    letter-spacing: 1px;
  }
}

@media (max-width: 540px) {
  .intro-text {
    padding: 60px;
  }
  .intro-text h1 {
    font-size: 3rem;
  }
  .intro-text h2 {
    font-size: 2rem;
  }
  .intro-text small {
    font-size: 1rem;
  }
}

@media (max-width: 330px) {
  .intro-container img {
    max-width: 150px;
  }
  .intro-text {
    padding: 30px;
  }
  .intro-text h1 {
    font-size: 2rem;
  }
  .intro-text h2 {
    font-size: 1.5rem;
  }
  .intro-text small {
    font-size: 0.8rem;
  }
}

