.field-container {
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  width: 100%;
  padding: 40px 60px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  font-family: 'Jura', sans-serif;
}

.field-container a {
  color: var(--main-purple);
}

.field-container p {
  font-size: 1.3rem;
}

.field-container ul {
  margin: 30px 0;
}

.field-container li {
  margin: 20px 0;
}

.field-logo {
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.field-logo img {
  max-width: 200px;
}

@media (max-width: 540px) {
  .field-container {
    padding: 20px 30px;
  }
  .field-logo {
    margin-bottom: 0;
  }
  .field-logo img {
    max-width: 150px;
  }
  .field-container h1 {
    font-size: 1.5rem;
  }
  .field-container p {
    font-size: 1rem;
  }
  .field-container ul {
    margin: 0;
  }
  .field-container li {
    margin: 5px 0;
  }
}
